import icalamot from './images/vida/calamothead.jpg'; // with import


//FONTAWESOME ICONS

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFunnelDollar } from '@fortawesome/free-solid-svg-icons';
import { faLeaf } from '@fortawesome/free-solid-svg-icons';

import { faBuilding } from '@fortawesome/free-solid-svg-icons';
import { faHiking } from '@fortawesome/free-solid-svg-icons';
import { faKiwiBird } from '@fortawesome/free-solid-svg-icons';



//Tab menu icon declaration

const iFund = <FontAwesomeIcon icon={faFunnelDollar}  size="2x"/>;
const iLeaf = <FontAwesomeIcon icon={faLeaf}  size="2x"/>;


const iBuilding = <FontAwesomeIcon icon={faBuilding}  size="2x"/>;
const iHike = <FontAwesomeIcon icon={faHiking}  size="2x"/>;
const iKiwi = <FontAwesomeIcon icon={faKiwiBird}  size="2x"/>;

const connector =
[{
        src: icalamot,
        thumbnail: icalamot,
        thumbnailWidth: 1024,
        thumbnailHeight: 430,
        caption: "El connector amb la platja"
}]

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      <Box p={3} justifyContent="center">{children}</Box>
    </Typography>
  );
}
TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};
function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}


function MenuTabs(props) {
  const [value, setValue] = React.useState(1);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <div >
      <Box  >
        <Tabs
          className="tab"
          indicatorColor="primary"
          textColor="primary"
          value={value}
          onChange={handleChange}
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label={<strong>Vida al Calamot</strong>} icon={iKiwi} {...a11yProps(0)} />
          <Tab label={<strong>Corredor natural</strong>} icon={iLeaf} {...a11yProps(1)} />
          <Tab label={<strong>Salvem el Calamot</strong>} icon={iHike} {...a11yProps(2)} />
          <Tab label={<strong> Pla de ponent</strong>} icon={iBuilding} {...a11yProps(3)} />
          <Tab label={<strong color="disabled">Especulació inmobiliaria</strong>} icon={iFund} {...a11yProps(4)} />

        </Tabs>
      </Box>

      <Box >
      <TabPanel value={value} index={0}>
        <FirstTab />
      </TabPanel>

      <TabPanel value={value} index={1}>
        <SecondTab />
      </TabPanel>

      <TabPanel value={value} index={2}>
        <ThirdTab />
      </TabPanel>

      <TabPanel value={value} index={3}>
        <FourthTab />
      </TabPanel>

      <TabPanel value={value} index={4}>
        <FifthTab />
      </TabPanel>

      </Box>


    </div>
  );
}

function FirstTab(props) {
return (
  <React.Fragment>
    <Grid container justify = "center">
      <h2 className="heading"> La vida al Calamot </h2>
    </Grid>
    <Grid container spacing={3} alignItems="center" justify="center">
      <Vida/>
    </Grid>
  </React.Fragment>
);
}

